.app-home {

    &__container {
        position: relative;
    }

    &__top {
        background: none;
        padding-left: 0;
        padding-right: 0;

        @media (max-width:$tablet-max) {
            padding: 32px 0;
        }

        @media (max-width:$smallTablet-max) {
            padding: 24px 0;
        }
    }

    &__title {
        @include adaptiveValue("font-size", 132, 38, 1296, 420, 1);
        letter-spacing: -3.24px;
        font-weight: 600;

        @media (max-width:$tablet-max) {
            letter-spacing: -2px;
        }
    }

    &__text {
        @media (max-width:$tablet-max) {
            top: 10px;
        }

        p {
            max-width: 750px;

            @media (max-width:$mobile-max) {
                display: inline;
            }
        }
    }

    &__form {
        max-width: 518px;
        margin: 0 auto;

        @media (max-width:$tablet-max) {
            margin-top: 16px;
        }

        @media (max-width:$smallTablet-max) {
            .label-primary {
                height: 48px;

                &__input {
                    height: 48px;
                }
            }
        }
    }

    &__btn {
        margin-top: 40px;
        height: 76px;
        @include adaptiveValue("border-radius", 28, 18, 1296, 420, 1);

        @media (max-width:$tablet-max) {
            margin-top: 24px;
        }
    }

    &__decor-lightning {
        position: absolute;
        z-index: 1;

        &--1 {
            top: 2.5%;
            left: 20%;
        }

        &--2 {
            top: 35%;
            left: 10%;
        }

        &--3 {
            top: 51%;
            left: 4%;
        }

        &--4 {
            top: 75%;
            left: 16%;
        }

        &--5 {
            top: 28%;
            right: -3%;
            z-index: -1;
        }

        &--6 {
            top: 56%;
            right: 17%;
        }

        @media (max-width:$tablet-max) {
            display: none;
        }
    }
}
